.widget-creation {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;

  & button {
    width: 30%;
    background-color: rgba(66, 98, 255, 1);
    border: none;
    color: white;
    padding: .4rem;
    font-size: 12px;
    border-radius: 4px;

    & > svg {
      width: 2rem;
      height: auto;
      color: white;
      margin-right: 4px;
    }
  }
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
